@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-semibold.woff2") format("woff2"), url("../fonts/alumnisans-semibold.woff") format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-bold.woff2") format("woff2"), url("../fonts/alumnisans-bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-extrabold.woff2") format("woff2"), url("../fonts/alumnisans-extrabold.woff") format("woff");
  font-style: normal;
  font-weight: 800;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumnisans-black.woff2") format("woff2"), url("../fonts/alumnisans-black.woff") format("woff");
  font-style: normal;
  font-weight: 900;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-light.woff2") format("woff2"), url("../fonts/robotocondensed-light.woff") format("woff");
  font-style: normal;
  font-weight: 300;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../../fonts/robotocondensed-regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 14px;
  }
}

.btn {
  display: inline-flex;
  vertical-align: middle;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.main-header {
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  position: relative;
  display: flex;
  justify-content: center;
  padding-top: 31px;
  padding-bottom: 52px;
}
@media (max-width: 1199px) {
  .main-header {
    padding-top: 33px;
    padding-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding-top: 15px;
    padding-bottom: 78px;
  }
}

.main-nav {
  max-width: 1237px;
  width: 100%;
}

.main-nav__top {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .main-nav__top {
    padding-bottom: 20px;
  }
}

.main-nav__logo {
  margin: 7px 0 0 0;
}
@media (max-width: 1199px) {
  .main-nav__logo {
    width: 82px;
    height: 17px;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .main-nav__logo {
    width: 68px;
    height: 23px;
    margin-top: 0;
  }
}

.main-nav__logo svg {
  fill: #ffffff;
}

.main-nav__phone a {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .main-nav__phone a {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .main-nav__phone a {
    font-size: 20px;
    line-height: 23px;
  }
}
.main-nav__phone a:hover {
  color: #102654;
}
.main-nav__phone a:active {
  color: #102654;
}
.main-nav__phone a:focus {
  color: #102654;
  outline: none;
}

.main-nav__description {
  display: flex;
  width: 100%;
  gap: 65px;
}
@media (max-width: 1199px) {
  .main-nav__description {
    gap: 30px;
    max-width: 800px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .main-nav__description {
    max-width: 500px;
  }
}

.main-nav__image {
  margin: -17px 199px 0 auto;
  max-width: 500px;
  height: auto;
  flex-shrink: 0;
}
@media (max-width: 1199px) {
  .main-nav__image {
    margin: 0 0 0 auto;
    max-width: 324px;
  }
}
@media (max-width: 767px) {
  .main-nav__image {
    display: none;
  }
}

.main-nav__image img {
  width: 100%;
  height: auto;
  object-fit: contain;
}
@media (max-width: 1199px) {
  .main-nav__image img {
    aspect-ratio: 324/390;
    object-fit: cover;
  }
}

.logo__image {
  width: 100%;
  height: 100%;
}

.nav-description__city,
.nav-description__title {
  margin: 0;
}

.nav-description__wrapper {
  padding: 93px 0 0 100px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1199px) {
  .nav-description__wrapper {
    padding: 50px 0 0 100px;
    width: auto;
  }
}
@media (max-width: 767px) {
  .nav-description__wrapper {
    padding: 50px 0 0 0;
    width: 100%;
  }
}

.nav-description__city {
  margin: 0 50px 0 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  text-align: right;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .nav-description__city {
    margin: 0 4px 0 0;
    font-size: 34px;
    line-height: 41px;
    text-align: right;
  }
}
@media (max-width: 767px) {
  .nav-description__city {
    font-size: 28px;
    line-height: 34px;
    margin: 0 3px 0 0;
  }
}

.nav-description__block {
  display: flex;
  align-items: flex-end;
  gap: 10px;
  position: relative;
  padding-left: 0;
}
@media (max-width: 767px) {
  .nav-description__block {
    padding: 12px 0 0 10px;
    width: 100%;
    display: block;
  }
}

.nav-description__title {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  margin-top: 15px;
  text-transform: uppercase;
  color: #ffffff;
  -webkit-text-stroke-width: 2px;
}
@media (max-width: 1199px) {
  .nav-description__title {
    font-size: 72px;
    line-height: 86px;
    text-align: right;
    text-transform: none;
    letter-spacing: 0.05em;
    margin: 6px 0 0 0;
    max-width: 280px;
  }
}
@media (max-width: 767px) {
  .nav-description__title {
    margin: 0 auto;
  }
}

.nav-description__list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: max-content;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  bottom: 30px;
  left: -80px;
}
@media (max-width: 1199px) {
  .nav-description__list {
    text-align: right;
    align-self: flex-end;
    bottom: 16px;
    left: -99px;
  }
}
@media (max-width: 767px) {
  .nav-description__list {
    left: 0;
    bottom: 27px;
  }
}

.nav-description__item {
  width: max-content;
}

.nav-description__item a {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: #ffffff;
}

.nav-description__btn {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 4px solid #ffffff;
  margin-top: 48px;
  padding: 20px 54px;
  background: transparent;
  max-width: 233px;
  align-self: flex-end;
  justify-content: center;
}
.nav-description__btn:hover, .nav-description__btn:focus, .nav-description__btn:active {
  background-color: #14367a;
  border: 4px solid #ed0233;
}
.nav-description__btn:focus {
  outline: none;
}
.nav-description__btn:active {
  color: #ed0233;
}
@media (max-width: 1199px) {
  .nav-description__btn {
    margin-top: 35px;
    margin-left: auto;
    padding: 21px 30px;
  }
}
@media (max-width: 767px) {
  .nav-description__btn {
    padding: 22px 20px 18px;
    width: 100%;
    margin-top: 23px;
    max-width: 100%;
  }
}

